.App {
  text-align: center;
}
._leftPanel {
  /* border: 3px solid white; */
  list-style: none;
  padding: 1em;
  display: grid;
  grid-template-areas: 
    "title1 title1 title1"
    "content content content"
    "title2 title2 image"
    "cards cards image";
}
._leftPanel > h3 {
  grid-area: title1;
}
._leftPanel > p {
  grid-area: content;
}
._leftPanel > h4 {
  grid-area: title2;
}
._leftPanel > img {
  grid-area: image;
}
._leftPanel > ul {
  grid-area: cards;
}
.logo {
  margin-top: 4vh;
  animation-name: spin;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.navbar {
  padding: 0 1vw;
  margin: 0;
}
.dropdown-menu {
  margin-top: 0;
}
.dropdown-toggle {
  background: none;
  border: none;
}
.dropdown-toggle:focus + *, .dropdown-menu:hover {
  display: block;
}
.dropdown:hover > button, .dropdown-toggle:focus {
  background-color: #df691a;
}
._card {
  margin: 0;
  display: flex;
  flex-direction: row;
  margin: 0 0 calc(1vh + 1vw) 0;
}
._card > p {
  margin: 0 10px;
  display: flex;
  align-items: center;
}
